
border-none():after {
  display: none;
}
.tab {
  border-bottom: 1px solid rbga(7, 17, 27, 0.2);
  height: 1.06667rem;
}
.tab .tab-item {
    text-align: center;
    font-size: 14px;
    line-height: 14px;
}
[data-dpr="2"] .tab .tab-item {
      font-size: 28px;
}
[data-dpr="3"] .tab .tab-item {
      font-size: 42px;
}
[data-dpr="2"] .tab .tab-item {
      line-height: 28px;
}
[data-dpr="3"] .tab .tab-item {
      line-height: 42px;
}
.tab .tab-item a {
      color: #4d555d;
}
.tab .tab-item .active {
      color: #f01414;
}



