
border-none():after {
  display: none;
}
.split {
  width: 100%;
  height: 0.42667rem;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  background: #f3f5f7;
}
.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 1.28rem;
  z-index: 100;
  width: 100%;
  background: #fff;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food.move-enter-active, .food.move-leave-active {
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
}
.food.move-enter, .food.move-leave-active {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
.food .image-header {
    width: 100%;
    height: 9.33333rem;
}
.food .image-header img {
      display: block;
      width: 100%;
      height: 100%;
}
.food .back {
    position: absolute;
    top: 0.26667rem;
    left: 0;
}
.food .back .icon-arrow_lift {
      display: block;
      padding: 0.26667rem;
      font-size: 20px;
      color: #fff;
}
[data-dpr="2"] .food .back .icon-arrow_lift {
        font-size: 40px;
}
[data-dpr="3"] .food .back .icon-arrow_lift {
        font-size: 60px;
}
.food .content {
    position: relative;
    padding: 0.48rem;
}
.food .content .title {
      margin-bottom: 0.21333rem;
      font-size: 14px;
      font-weight: 700;
      color: #07111b;
}
[data-dpr="2"] .food .content .title {
        font-size: 28px;
}
[data-dpr="3"] .food .content .title {
        font-size: 42px;
}
.food .content .detail {
      margin-bottom: 0.48rem;
      height: 0.26667rem;
}
.food .content .detail .sell-count, .food .content .detail .rating {
        font-size: 10px;
        color: #93999f;
}
[data-dpr="2"] .food .content .detail .sell-count, [data-dpr="2"] .food .content .detail .rating {
          font-size: 20px;
}
[data-dpr="3"] .food .content .detail .sell-count, [data-dpr="3"] .food .content .detail .rating {
          font-size: 30px;
}
.food .content .detail .sell-count {
        margin-right: 0.32rem;
}
.food .content .price {
      font-weight: 700;
}
.food .content .price .now {
        margin-right: 0.21333rem;
        font-size: 14px;
        color: #f01414;
}
[data-dpr="2"] .food .content .price .now {
          font-size: 28px;
}
[data-dpr="3"] .food .content .price .now {
          font-size: 42px;
}
.food .content .price .old {
        text-decoration: line-through;
        font-size: 10px;
        color: #93999f;
}
[data-dpr="2"] .food .content .price .old {
          font-size: 20px;
}
[data-dpr="3"] .food .content .price .old {
          font-size: 30px;
}
.food .content .cartcontrol-wrapper {
      position: absolute;
      right: 0.48rem;
      bottom: 0;
}
.food .content .buy {
      position: absolute;
      right: 0.48rem;
      bottom: 0.48rem;
      z-index: 10;
      line-height: 24px;
      padding: 0 0.32rem;
      box-sizing: border-box;
      border-radius: 0.26667rem;
      font-size: 10px;
      color: #fff;
      background: #00a0dc;
      opacity: 1;
}
[data-dpr="2"] .food .content .buy {
        line-height: 48px;
}
[data-dpr="3"] .food .content .buy {
        line-height: 72px;
}
[data-dpr="2"] .food .content .buy {
        font-size: 20px;
}
[data-dpr="3"] .food .content .buy {
        font-size: 30px;
}
.food .content .buy.fade-enter-active, .food .content .buy.fade-leave-active {
        -webkit-transition: all 0.2s;
        transition: all 0.2s;
}
.food .content .buy.fade-enter, .food .content .buy.fade-leave-active {
        opacity: 0;
        z-index: -1;
}
.food .info {
    padding: 0.48rem;
}
.food .info .title {
      font-size: 14px;
      margin-bottom: 0.16rem;
      color: #07111b;
}
[data-dpr="2"] .food .info .title {
        font-size: 28px;
}
[data-dpr="3"] .food .info .title {
        font-size: 42px;
}
.food .info .text {
      padding: 0 0.21333rem;
      font-size: 12px;
      line-height: 15px;
      color: #4d555d;
}
[data-dpr="2"] .food .info .text {
        font-size: 24px;
}
[data-dpr="3"] .food .info .text {
        font-size: 36px;
}
[data-dpr="2"] .food .info .text {
        line-height: 30px;
}
[data-dpr="3"] .food .info .text {
        line-height: 45px;
}
.food .rating {
    padding-top: 0.48rem;
}
.food .rating .title {
      margin-left: 0.48rem;
      font-size: 14px;
      color: #07111b;
}
[data-dpr="2"] .food .rating .title {
        font-size: 28px;
}
[data-dpr="3"] .food .rating .title {
        font-size: 42px;
}
.food .rating .rating-wrapper {
      padding: 0 0.48rem;
}
.food .rating .rating-wrapper .rating-item {
        position: relative;
        padding: 0.48rem 0;
        border: 1px solid rgba(7, 17, 27, 0.1);
}
.food .rating .rating-wrapper .rating-item .user {
          position: absolute;
          right: 0;
          top: 0.42667rem;
}
.food .rating .rating-wrapper .rating-item .user .name {
            display: inline-block;
            margin-right: 0.16rem;
            vertical-align: top;
            font-size: 10px;
            color: #93999f;
}
[data-dpr="2"] .food .rating .rating-wrapper .rating-item .user .name {
              font-size: 20px;
}
[data-dpr="3"] .food .rating .rating-wrapper .rating-item .user .name {
              font-size: 30px;
}
.food .rating .rating-wrapper .rating-item .user .avatar {
            border-radius: 50%;
}
.food .rating .rating-wrapper .rating-item .time {
          margin-bottom: 0.16rem;
          font-size: 10px;
          color: #93999f;
}
[data-dpr="2"] .food .rating .rating-wrapper .rating-item .time {
            font-size: 20px;
}
[data-dpr="3"] .food .rating .rating-wrapper .rating-item .time {
            font-size: 30px;
}
.food .rating .rating-wrapper .rating-item .text {
          line-height: 16px;
          font-size: 12px;
          color: #07111b;
}
[data-dpr="2"] .food .rating .rating-wrapper .rating-item .text {
            line-height: 32px;
}
[data-dpr="3"] .food .rating .rating-wrapper .rating-item .text {
            line-height: 48px;
}
[data-dpr="2"] .food .rating .rating-wrapper .rating-item .text {
            font-size: 24px;
}
[data-dpr="3"] .food .rating .rating-wrapper .rating-item .text {
            font-size: 36px;
}
.food .rating .rating-wrapper .rating-item .text .icon-thumb_up, .food .rating .rating-wrapper .rating-item .text .icon-thumb_down {
            margin-right: 0.10667rem;
            font-size: 16px;
            font-size: 12px;
}
[data-dpr="2"] .food .rating .rating-wrapper .rating-item .text .icon-thumb_up, [data-dpr="2"] .food .rating .rating-wrapper .rating-item .text .icon-thumb_down {
              font-size: 32px;
}
[data-dpr="3"] .food .rating .rating-wrapper .rating-item .text .icon-thumb_up, [data-dpr="3"] .food .rating .rating-wrapper .rating-item .text .icon-thumb_down {
              font-size: 48px;
}
[data-dpr="2"] .food .rating .rating-wrapper .rating-item .text .icon-thumb_up, [data-dpr="2"] .food .rating .rating-wrapper .rating-item .text .icon-thumb_down {
              font-size: 24px;
}
[data-dpr="3"] .food .rating .rating-wrapper .rating-item .text .icon-thumb_up, [data-dpr="3"] .food .rating .rating-wrapper .rating-item .text .icon-thumb_down {
              font-size: 36px;
}
.food .rating .rating-wrapper .rating-item .text .icon-thumb_up {
            color: #00a0dc;
}
.food .rating .rating-wrapper .rating-item .text .icon-thumb_down {
            color: #93999f;
}



