
border-none():after {
  display: none;
}
.ratingselect {
  padding-left: 0.48rem;
  padding-top: 0.48rem;
}
.ratingselect .rating-type {
    padding: 0 0.48rem 0.48rem 0;
    border-bottom: 1px solid #e6e7e8;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.ratingselect .rating-type .block {
      display: block;
      padding: 0.24rem 0.29333rem;
      background: #ccecf8;
      color: #4d555d;
      margin-right: 0.21333rem;
      border-radius: 0.10667rem;
      font-size: 12px;
}
[data-dpr="2"] .ratingselect .rating-type .block {
        font-size: 24px;
}
[data-dpr="3"] .ratingselect .rating-type .block {
        font-size: 36px;
}
.ratingselect .rating-type .block.active {
        background: #00a0dc;
        color: #fff;
}
.ratingselect .rating-type .block.negative {
        background: #e9ebec;
        margin-right: 0;
}
.ratingselect .rating-type .block.negative.active {
          background: #4d555d;
          color: #fff;
}
.ratingselect .rating-type .block > .count {
        margin-left: 0.10667rem;
        font-size: 9px;
}
[data-dpr="2"] .ratingselect .rating-type .block > .count {
          font-size: 18px;
}
[data-dpr="3"] .ratingselect .rating-type .block > .count {
          font-size: 27px;
}
.ratingselect .switch {
    color: #93999f;
}
.ratingselect .switch .icon-check_circle {
      font-size: 24px;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
      padding: 0.37333rem 0;
}
[data-dpr="2"] .ratingselect .switch .icon-check_circle {
        font-size: 48px;
}
[data-dpr="3"] .ratingselect .switch .icon-check_circle {
        font-size: 72px;
}
.ratingselect .switch.on .icon-check_circle {
      color: #00c850;
}
.ratingselect .switch .text {
      display: inline-block;
      vertical-align: top;
      font-size: 12px;
      line-height: 24px;
      color: #93999f;
}
[data-dpr="2"] .ratingselect .switch .text {
        font-size: 24px;
}
[data-dpr="3"] .ratingselect .switch .text {
        font-size: 36px;
}
[data-dpr="2"] .ratingselect .switch .text {
        line-height: 48px;
}
[data-dpr="3"] .ratingselect .switch .text {
        line-height: 72px;
}



