
border-none():after {
  display: none;
}
.controler {
  position: absolute;
  right: 0;
  bottom: 0.26667rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.controler .num {
    display: block;
    font-size: 11px;
    line-height: 24px;
    color: #93999f;
    width: 0.64rem;
    text-align: center;
}
[data-dpr="2"] .controler .num {
      font-size: 22px;
}
[data-dpr="3"] .controler .num {
      font-size: 33px;
}
[data-dpr="2"] .controler .num {
      line-height: 48px;
}
[data-dpr="3"] .controler .num {
      line-height: 72px;
}
.controler .min, .controler .plus {
    display: block;
    color: #00a0dc;
    font-size: 24px;
    line-height: 24px;
    -webkit-transform: translate3d(0, 0, 0) rotate(0);
            transform: translate3d(0, 0, 0) rotate(0);
    opacity: 1;
}
[data-dpr="2"] .controler .min, [data-dpr="2"] .controler .plus {
      font-size: 48px;
}
[data-dpr="3"] .controler .min, [data-dpr="3"] .controler .plus {
      font-size: 72px;
}
[data-dpr="2"] .controler .min, [data-dpr="2"] .controler .plus {
      line-height: 48px;
}
[data-dpr="3"] .controler .min, [data-dpr="3"] .controler .plus {
      line-height: 72px;
}
.controler .min {
    padding: 0.24rem 0 0.24rem 0.24rem;
}
.controler .plus {
    padding: 0.24rem 0.24rem 0.24rem 0;
}
.controler .move-enter-active, .controler .move-leave-active {
    -webkit-transition: all ease .5s;
    transition: all ease .5s;
}
.controler .move-enter, .controler .move-leave-active {
    opacity: 0;
    -webkit-transform: translate3d(0.8rem, 0, 0) rotate(180deg);
            transform: translate3d(0.8rem, 0, 0) rotate(180deg);
}



